export default class FindPath {
  constructor(option) {
    let {
      width = 20,
      height = 20
    } = option
    this.width = width
    this.height = height
    this.start = null
    this.end = null
    this.queue = []
    this.vis = []
    this.pre = []
    this.g = []
    this.dir = [
      [0, 1],
      [0, -1],
      [1, 0],
      [-1, 0]
    ]
  }
  getNextNode (snake, food) {
    this.snake = snake
    this.build(snake, food)
    
    //console.log('food', food);  
    let next = this.Astar(this.cmpMin)
    // let next = this.bfs()
    // 
    if (this.isSecurity(next)) {
      //console.log('')
      return {
        flag: true,
        node: next.pop()
      }
    } else {
      // 
      // 
      this.build(snake, snake[snake.length - 1])
      
      // 
      this.vis[food.index] = true
      next = this.Astar(this.cmpMax)
      
      // 
      if (next.length === 0) {
        // 
        //console.log('', this.randomStep(snake));
        return {
          flag: true,
          node: this.randomStep(snake).pop()
        }
      } else {
        //console.log('')
        
        return {
          flag: true,
          node: next.pop()
        }
      }
    }
    // 
    return {
      flag: false,
      node: null
    }
  }
  /**
   * 
   *
   * @memberof FindPath
   */
  init () {
    this.start = null
    this.end = null
    this.queue = []
    this.vis = []
    this.pre = []
  }
  /**
   * 
   *
   * @param {*} snake 
   * @param {*} food 
   * @memberof FindPath
   */
  build (snake, food) {
    this.init()
    // 
    this.start = Object.assign({}, snake[0])
    this.end = Object.assign({}, food)
    // 
    let len = snake.length
    for (let i = 0; i < len; i++) {
      this.vis[snake[i].index] = true
    }
    // 
    // this.vis[food.index] = false
  }
  /**
   * 
   *
   * @memberof FindPath
   */
  reconstructPath () {
    let index = this.end.index
    // 
    let next = []
    next.push(this.end)
    while (this.pre[index] && this.pre[index].index !== this.start.index) {
      next.push(this.pre[index])
      index = this.pre[index].index
    }
    return next
  }
  /**
   * 
   *
   * @param {*} node
   * @memberof FindPath
   */
  judge (node) {
    return (!this.vis[node.index] || this.end.index === node.index) 
        && node.x >= 0 && node.x < this.width 
        && node.y >= 0 && node.y < this.height
  }
  /**
   * 
   *
   * @param {*} p 
   * @param {*} q
   * @memberof FindPath
   */
  h (p, q) {
    return Math.abs(q.x - p.x) + Math.abs(q.y - p.y)
  }
  getEndH (node) {
    return this.h(node, this.end)
  }
  /**
   * A*
   *
   * @param {*} cmp 
   * @memberof FindPath
   */
  Astar (cmp) {
    let q = this.start
    let p = {}
    q.g = 0
    q.h = this.getEndH(q)
    q.f = q.h
    this.queue.push(q)
    while (this.queue.length) {
      q = this.queue.shift()
      // ,
      if (q.index === this.end.index) {
        return this.reconstructPath(q)
      }
      for (let i = 0; i < 4; i++) {
        p.x = q.x + this.dir[i][0]
        p.y = q.y + this.dir[i][1]
        p.index = p.y * this.width + p.x
        if (this.judge(p)) {
          // 
          this.pre[p.index] = { ...q }
          // 
          this.vis[p.index] = true
          // ghf
          p.g = q.g + 1
          p.h = this.getEndH(p)
          p.f = p.g + p.h
          this.queue.push({ ...p })
        }
      }
      // Hack  TODO: 
      this.queue.sort((a, b) => cmp(a.f, b.f))
    }
    // 
    return []
  }
  randomStep (snake) {
    this.build(snake, snake[snake.length - 1])
    let q = this.start
    let p = {}
    let num = [0, 1, 2, 3]
    // 
    // this.shuffle(num)
    for (let i = 0; i < 4; i++) {
      p.x = q.x + this.dir[num[i]][0]
      p.y = q.y + this.dir[num[i]][1]
      p.index = p.y * this.width + p.x

      if (this.judge(p)) {
        return [p]
      }
    }
    return []
  }
  /**
   * 
   *
   * @memberof FindPath
   */
  isSecurity (path) {
    // 
    if(path.length === 0) return false
    // 10
    if(this.snake.length < 10) return true
    let newSnakeBody = []
    if (path.length >= this.snake.length + 1) {
      // 
      newSnakeBody = path.slice(0, this.snake.length + 1)
    } else {
      newSnakeBody = path.concat(this.snake.slice(0, this.snake.length - (path.length-1)))
    }
    //console.log('path', JSON.parse(JSON.stringify(path)));
    //console.log('snake', JSON.parse(JSON.stringify(this.snake)));
    //console.log('new', JSON.parse(JSON.stringify(newSnakeBody)));
    // return true
    this.build(newSnakeBody, newSnakeBody[newSnakeBody.length - 1])
    let pat = this.Astar(this.cmpMax)
    // if(pat.length === 0) //console.log('!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!')
    return  pat.length > 0
  }
  /**
   * 
   *
   * @param {*} a
   * @param {*} b
   * @returns
   * @memberof FindPath
   */
  cmpMin (a, b) {
    return a - b
  }
  /**
   * 
   *
   * @param {*} a
   * @param {*} b
   * @returns
   * @memberof FindPath
   */
  cmpMax (a, b) {
    return b - a
  }
  /**
   * 
   *
   * @param {*} arr
   * @memberof FindPath
   */
  shuffle (arr) {
    let i = arr.length;
    while (i) {
      let j = Math.floor(Math.random() * i--);
      [arr[j], arr[i]] = [arr[i], arr[j]];
    }
  }
  /**
   * 
   *
   * @returns
   * @memberof FindPath
   */
  bfs () {
    let q = this.start
    let p = {}
    this.queue.push(q)
    while (this.queue.length) {
      q = this.queue.shift()
      for (let i = 0; i < 4; i++) {
        p.x = q.x + this.dir[i][0]
        p.y = q.y + this.dir[i][1]
        p.index = p.y * this.width + p.x
        if (this.judge(p)) {
          // 
          this.pre[p.index] = { ...q }
          // 
          if (p.index === this.end.index) {
            return this.reconstructPath(p)
          }
          // 
          this.vis[p.index] = true
          this.queue.push({ ...p })
        }
      }
    }
    // 
    return []
  }
}